  font-family: "edosz";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
body {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: 70%;
  color: black;
  font-family: sans-serif;
  margin: 0px;
  padding: 0px;
  align-items: center;
  justify-content: center;
}
body header #regularNavbar a {
  font-family: "edosz";
}
body header #regularNavbar a:hover {
  background-color: gold;
}
body header #banner-img {
  height: auto;
  max-width: 100%;
}
body header #collapsibleNavbar {
  font-family: "edosz";
}
body header #collapsibleNavbar a {
  padding: 0px;
  margin-left: 1vw;
  text-decoration: none;
  color: #00b300;
  font-size: 2.2vw;
}
body header #collapsibleNavbar a:hover {
  color: #b4f298;
  font-size: 2.3vw;
}
body header #collapsibleNavbar ul {
  margin-left: 1vw;
}
body header #collapsibleNavbar #homeNavDiv a {
  font-size: 3vw;
}
body header #collapsibleNavbar #signInContent {
  font-family: Arial, Helvetica, sans-serif;
}
body h1,
body h2,
body h3 {
  font-family: "edosz";
}

footer {
  color: rgb(255, 255, 255);
  background-color: #181818;
  text-align: center;
  margin: 0px;
  padding: 0px;
  align-items: center;
  justify-content: center;
  opacity: 0.9;
}

.no-bullets {
  list-style-type: none;
}

.no-decor {
  text-decoration: none;
}

.w100 {
  width: 100%;
}

#siteMapGroup {
  background-color: #181818;
  text-align: center;
  opacity: 0.9;
}
#siteMapGroup .list-group-item {
  width: 30%;
  background-color: #404040;
}
#siteMapGroup .list-group {
  font-weight: bold;
  align-items: center;
}

.cardButton {
  transform: scale(1);
  transition: transform 1s;
}
.cardButton:hover {
  transform: scale(1.3);
  transition: transform 2s;
}

.border-dashed {
  border-style: dashed !important;
}

.border-dotted {
  border-style: dotted !important;
